

















import Vue from 'vue';
import {Component, } from 'vue-property-decorator';

@Component
export default class MaskDiv extends Vue {
  notes = ''

  closeMask() {
    this.$emit('closeMask', {display:'none'})
  }
  sureText(notes: string){
    if(notes !== ''){
      this.$emit('sureText', notes.trim())
    }
  }
}
